import React, { Component } from "react";
import { FaTimes } from "react-icons/fa";
import Moment from "react-moment";
import ReactTooltip from "react-tooltip";

class ListAppointments extends Component {
  render() {
    return (
      <div className="appointment-list item-list mb-3">
        {this.props.appointments.map((item) => (
          <div className="pet-item col media py-3" key={item.aptId}>
            <div className="mr-3">
              <button
                className="pet-delete btn btn-sm btn-danger"
                onClick={() => this.props.deleteAppointment(item)}
                data-tip="Delete Appointment"
                data-place="top"
              >
                <FaTimes />
                <ReactTooltip effect="solid" arrowColor="transparent" />
              </button>
            </div>

            <div className="pet-info media-body">
              <div className="pet-head d-flex">
                <span
                  className="pet-name"
                  contentEditable
                  suppressContentEditableWarning
                  onBlur={(e) =>
                    this.props.updateInfo(
                      "petName",
                      e.target.innerText,
                      item.aptId
                    )
                  }
                  data-tip="Edit Name"
                data-place="top"
                >
                  {item.petName}
                <ReactTooltip effect="solid" arrowColor="transparent" />
                </span>
                <span className="apt-date ml-auto" data-tip="Edit Date"
                data-place="top">
                  <Moment
                    date={item.aptDate}
                    parse="YYYY-MM-dd hh:mm"
                    format="MMM-D h:mma"
                  />
                <ReactTooltip effect="solid" arrowColor="transparent" />
                </span>
              </div>

              <div className="owner-name">
                <span className="label-item">Owner name: <br className="mobile-only" /></span>
                <span
                  contentEditable
                  suppressContentEditableWarning
                  onBlur={(e) =>
                    this.props.updateInfo(
                      "ownerName",
                      e.target.innerText,
                      item.aptId
                    )
                  }
                >
                  {item.ownerName}
                </span>
              </div>
              <div
                className="apt-notes"
                contentEditable
                suppressContentEditableWarning
                onBlur={(e) =>
                  this.props.updateInfo(
                    "aptNotes",
                    e.target.innerText,
                    item.aptId
                  )
                }
              >
                <br className="mobile-only" />{item.aptNotes}
              </div>
            </div>
          </div>
        ))}
      </div>
    );
  }
}

export default ListAppointments;
