import React from "react";

const UpvotesSection = ({ articleName, upvotes, setArticleInfo }) => {
  const upvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvote`, {
      method: "post",
    });
    const body = await result.json();
    setArticleInfo(body);
  };
  return (
    <div id="upvotes-section" className="mt-5 mb-3">
      <p className="text-secondary">This post has been upvoted {upvotes} times</p>

      <button className="article-button" onClick={() => upvoteArticle()}>
        Add Upvote
      </button>
    </div>
  );
};

export default UpvotesSection;
